'use client'

import Image from 'next/image'
import Link from 'next/link'
import { useSession } from 'next-auth/react'
import { UserCircle } from 'lucide-react'
import { cn } from '@/lib/utils'

export default function DashboardLayout({
  children,
}: {
  children: React.ReactNode
}) {
  const { data: session } = useSession()

  return (
    <div className="h-screen flex flex-col">
      {/* Header */}
      <header className="w-full border-b bg-white">
        <div className="flex h-16 items-center justify-between px-4 sm:px-6">
          <div className="flex items-center gap-8">
            <Link href={session?.user?.userType === 'startup' ? '/startup/dashboard' : '/service-provider/dashboard'}>
              <Image
                src="/logo.png"
                alt="Cumma Logo"
                width={150}
                height={32}
                priority
              />
            </Link>
          </div>
          <div className="flex items-center gap-4">
            <span className="text-sm font-medium">Welcome, Back!</span>
            <div className="h-8 w-8 rounded-full bg-primary text-white flex items-center justify-center">
              <UserCircle className="h-6 w-6" />
            </div>
          </div>
        </div>
      </header>

      {/* Main Content Area */}
      <div className="flex-1 flex overflow-hidden">
        {/* Sidebar */}
        <aside className="w-[280px] bg-white border-r flex-shrink-0 overflow-y-auto">
          <div className="p-4 flex flex-col gap-8">
            {/* Main Navigation */}
            <div className="space-y-2">
              <h2 className="px-2 text-xs font-semibold text-gray-400 uppercase tracking-wider">
                MAIN
              </h2>
              <nav className="space-y-1">
                {navigation.main.map((item) => {
                  const isActive = pathname === item.href
                  return (
                    <Link
                      key={item.name}
                      href={item.href}
                      className={cn(
                        'flex items-center gap-3 px-2 py-3 text-sm font-medium rounded-lg transition-colors',
                        isActive
                          ? 'bg-primary text-white'
                          : 'text-gray-600 hover:bg-gray-50'
                      )}
                    >
                      <item.icon className="h-5 w-5" />
                      {item.name}
                    </Link>
                  )
                })}
              </nav>
            </div>

            {/* Account Management */}
            <div className="space-y-2">
              <h2 className="px-2 text-xs font-semibold text-gray-400 uppercase tracking-wider">
                MANAGE ACCOUNT
              </h2>
              <nav className="space-y-1">
                {navigation.account.map((item) => {
                  const isActive = pathname === item.href
                  if (item.onClick) {
                    return (
                      <a
                        key={item.name}
                        href={item.href}
                        onClick={(e) => {
                          e.preventDefault()
                          item.onClick()
                        }}
                        className={cn(
                          'flex items-center gap-3 px-2 py-3 text-sm font-medium rounded-lg transition-colors',
                          'text-gray-600 hover:bg-gray-50'
                        )}
                      >
                        <item.icon className="h-5 w-5" />
                        {item.name}
                      </a>
                    )
                  }
                  return (
                    <Link
                      key={item.name}
                      href={item.href}
                      className={cn(
                        'flex items-center gap-3 px-2 py-3 text-sm font-medium rounded-lg transition-colors',
                        isActive
                          ? 'bg-primary text-white'
                          : 'text-gray-600 hover:bg-gray-50'
                      )}
                    >
                      <item.icon className="h-5 w-5" />
                      {item.name}
                    </Link>
                  )
                })}
              </nav>
            </div>
          </div>
        </aside>

        {/* Page Content */}
        <main className="flex-1 overflow-y-auto bg-[#F8F9FC]">
          {children}
        </main>
      </div>
    </div>
  )
} 